}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}


.navbar {
    background-color: #403c3c;
    height: 50px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 2%

}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: #403c3c;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
}

.nav-menu.active{
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 50px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 15px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items:  center;
    padding: 0 12px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;
}
.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #403c3c;
    width: 75%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

span {
    margin-left: 12px;
}

